<template>
  <div class="control">
    <div class="item" @click="prePage" :class="{active:preActive}">上一页</div>
    <div class="item" @click="nextPage" :class="{active:nextActive}">下一页</div>
  </div>
</template>

<script>
export default {
  name:"BottomControl",
  data(){
    return {

    }
  },
  props:{
    preActive:{
      type:Boolean,
      default:false
    },
     nextActive:{
      type:Boolean,
      default:true
    }
  },
  methods: {
    nextPage(){
      this.$emit('nextPage');
    },
    prePage(){
      this.$emit('prePage');
    }
  }
}
</script>

<style scoped lang="less">
.control {
  display: flex;
  .item {
    flex: 1;
    text-align: center;
    color: #8b0000;
    line-height: 0.38rem;
    font-size: 0.16rem;
    background-color: #f3f3f4;
  }
  .active {
    background-color: #fff;
  }
}
</style>