<template>
  <div id="topic">
    <!-- 话题列表 -->
    <TopicList :topicList="topicList" />
    <!-- 底部的控制导航 -->
    <BottomControl
      @nextPage="nextPage"
      @prePage="prePage"
      :nextActive="nextActive"
      :preActive="preActive"
    />
  </div>
</template>
<script>
import {getTopicList} from "@/network/apis"
import BottomControl from "./childComps/BottomControl"
import TopicList from "./childComps/TopicList"
export default {
  name: "Topic",
  data(){
    return {
      // 请求的条数
      pageSize:5,
      // 请求的第几个页面
      currentPage:0,
      // 数据集合
      topicList:[],
      // 总的页数
      totalPages:0,
    };
  },
  components:{
    BottomControl,TopicList
  },
  methods:{
    getTopicList(params){
      getTopicList(params).then(res=>{
      console.log('Topic==>',res);
      if(res.errno===0){
          let {currentPage,data:topicList,pageSize,totalPages} = res.data;
          this.currentPage = currentPage;
          this.topicList = topicList;
          this.totalPages = totalPages;
          
          // 返回顶部
          document.documentElement.scrollTop = 0;
      }
    });
    },
    nextPage(){
      if(this.currentPage < this.totalPages){
        this.getTopicList({
        page:++this.currentPage,
        size:this.pageSize
      });
      }else{
        this.$toast({
        message: '已经是最后一页了~',
        position: 'top',
      });
      }
    },
    prePage(){
      if(this.currentPage-1>0){
        this.getTopicList({
        page:--this.currentPage,
        size:this.pageSize
      });
      }else{
        this.$toast({
        message: '已经是第一页了~',
        position: 'top',
      });
      }
    }
  },
  computed:{
    preActive(){
        return this.currentPage>1;
    },
    nextActive(){
      return this.currentPage<this.totalPages;
    }
  },
  created(){
    this.getTopicList({
      page:++this.currentPage,
      size:this.pageSize
    });
  }
}
</script>
<style scoped lang="less">
#topic {
  padding-bottom: 0.7rem;
}
</style>
